.reviews {
  //background: url("/images/del/review-bg.png") center no-repeat;
  .content-wrap {
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    flex-direction: column;
    @include respond-to(md) {
      flex-direction: row;
    }
  }

  &__info {
    margin-bottom: 36px;
    width: 290px;
    order: 6;
    @include respond-to(md) {
      margin-bottom: 0;
      width: 33.33333%;
      position: absolute;
      order: 0;
    }
  }
  &__image {
    margin-bottom: 20px;
    width: 290px;
    background: $gray-light;
    order: 0;
    @include respond-to(md) {
      margin-bottom: 0;
      width: 41.6666%;
      margin-left: 21%;
      order: 6;
    }
    img {
      display: block;
      margin: 0 auto;
    }
  }
  &__text {
    width: 290px;
    order: 12;
    @include respond-to(md) {
      width: 25%;
    }

    img {
      margin: 0 auto;
      display: block;
      @include respond-to(md) {
        margin: 0;
      }
    }
  }
  &__body {
    margin-top: 24px;
    font-size: 15px;
    font-weight: 400;
    line-height: 21px;
    text-align: center;
    font-family: 'PT Serif', serif;
    @include respond-to(md) {
      margin-top: 30px;
      text-align: left;
    }
  }

  .product-item {
    &__place {
      a {
        color: $gray-dark;
      }
    }
  }
}

.reviews-human {
  margin-top: 26px;
  display: flex;
  align-items: center;
  flex-direction: column;
  @include respond-to(md) {
    margin-top: 39px;
    flex-direction: row;
  }
  &__image {
    margin-bottom: 14px;
    margin-right: 16px;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    overflow: hidden;
    @include respond-to(md) {
      margin-bottom: 0;
    }
  }
  &__text {
    font-size: 13px;
    line-height: 20px;
    color: $dark;
    font-weight: 500;
    @include respond-to(md) {
      font-size: 14px;
    }
  }
  &__name {
    font-size: 12px;
    @include respond-to(md) {
      font-size: 14px;
    }
  }
  &__prof {
    font-weight: 400;
    line-height: 18px;
    font-size: 11px;
    font-family: $subFont;
    color: #878787;
    text-align: center;
    @include respond-to(md) {
      text-align: left;
      font-size: 13px;
    }
  }
}