aside {
  width: 100%;
  @include respond-to(md) {
    margin-bottom: 40px;
    position: sticky;
    top: 120px;
    width: auto;
  }
}
.sidebar {
  width: 280px;
  @include respond-to(md) {
    width: auto;
    //position: fixed;
  }

  &__back {
    margin-bottom: 30px;

    a {
      font-size: 13px;
      font-weight: 400;
      line-height: 20px;

      &:before {
        content: '';
        display: inline-block;
        margin-right: 12px;
        width: 25px;
        height: 25px;
        background:  url("/images/icons/back-to-all.svg") center no-repeat;
        background-size: contain;
        position: relative;
        top: 6px;
        left: 0;
        transition: all .4s;
      }

      &:hover {
        &:before {
          background: url("/images/icons/back-to-all_hover.svg") center no-repeat;
          background-size: contain;
        }
      }
    }

    &--desktop {
      display: none;
      @include respond-to(md) {
        display: block;
      }
    }

    &--mobile {
      margin: 0;
      padding: 8px 0 14px;
      display: block;
      text-align: center;
      border-bottom: 1px solid $gray;
      @include respond-to(md) {
        display: none;
      }
    }
  }

  &__menu {
    margin-bottom: 26px;

    li {
      padding: 0;
    }

    a {
      font-size: 13px;
      font-weight: 400;
      line-height: 30px;
      color: $pink;
    }

    &--desktop {
      display: none;
      @include respond-to(md) {
        display: block;
      }
    }

    &--mobile {
      display: block;
      @include respond-to(md) {
        display: none;
      }

      ul {
        display: flex;
        align-items: center;
        justify-content: center;

        li {
          padding: 0 3%;
          white-space: nowrap;
        }
      }
    }
  }

  &__share {
  }

  &__print {
    display: none;
    text-align: center;
    border: 1px solid $gray;
    border-radius: 3px;
    cursor: pointer;
    transition: border-color .5s linear;
    position: relative;
    @include respond-to(md) {
      display: block;
    }
    &:before {
      content: '';
      display: inline-block;
      margin-right: 10px;
      width: 21px;
      height: 16px;
      background: url("/images/icons/printer.svg") center no-repeat;
      background-size: contain;
      position: relative;
      top: 3px;
      transition: opacity .5s linear;
      opacity: 1;
    }
    &:after {
      content: '';
      display: inline-block;
      margin-right: 10px;
      width: 21px;
      height: 16px;
      background: url("/images/icons/printer_hover.svg") center no-repeat;
      background-size: contain;
      position: absolute;
      left: 23px;
      top: 14px;
      transition: opacity .5s linear;
      opacity: 0;
    }

    &:hover {
      border-color: $dark;

      a {
        color: $dark;
      }

      &:before {
        opacity: 0;
      }
      &:after {
        opacity: 1;
      }
    }

    a {
      font-size: 11px;
      font-weight: 400;
      line-height: 48px;
      text-transform: uppercase;
      letter-spacing: 0.55px;
      color: $pink;
      transition: color .5s linear;
    }
  }
}

.link-share {
  margin-bottom: 29px;

  &__top {
    padding: 16px 0;
    font-size: 11px;
    font-weight: 400;
    line-height: 20px;
    text-transform: uppercase;
    letter-spacing: 0.55px;
    color: #878787;
    text-align: center;
    border: 1px solid $gray;
  }

  &__links {
    display: flex;

    a {
      display: inline-block;
      width: 97px;
      height: 50px;
      border-right: 1px solid $gray;
      border-bottom: 1px solid $gray;
      @include respond-to(md) {
        width: 54px;
      }

      &:first-child {
        border-left: 1px solid $gray;
      }

      &:not(:last-child) {
      }
    }

    &--fb {
      background: url("/images/icons/fb.svg") center no-repeat;

      &:hover {
        background: $gray-light url("/images/icons/fb.svg") center no-repeat;
      }
    }

    &--vk {
      background: url("/images/icons/vk.svg") center no-repeat;

      &:hover {
        background: $gray-light url("/images/icons/vk.svg") center no-repeat;
      }
    }

    &--inst {
      background: url("/images/icons/insta.svg") center no-repeat;

      &:hover {
        background: $gray-light url("/images/icons/insta.svg") center no-repeat;
      }
    }
  }
}