.banner {
  padding: 95px 0 240px;
  background: #1f1f1f;
  background-size: cover;
  position: relative;
  overflow: hidden;
  @include respond-to(md) {
    padding: 194px 0 51px;
  }
  &__image {
    //height: 100%;
    //position: absolute;
    //transform: translateX(-50%);
    //left: 50%;
    //top: -10%;
    //z-index: -1;
    min-height: 100%;
    position: absolute;
    z-index: -1;
    width: 100%;
    top: -50%;
    left: -80%;
    @include respond-to(sm) {
      top: -50%;
      left: -20%;
    }
    @include respond-to(md) {
      top: -10%;
      left: 50%;
      transform: translateX(-60%);
    }
    @include respond-to(lg) {
      left: 50%;
      transform: translateX(-50%);
    }
    img {
      display: block;
      margin: 0 auto;
      height: 100%;
      max-width: none;
      position: relative;
      animation: bannerImg 20s infinite linear;
    }
  }
  h1 {
    color: $white;
    font-size: 34px;
    font-weight: 700;

    position: absolute;
    width: 100%;
    @include respond-to(sm) {
      font-size: 40px;
    }
    @include respond-to(md) {
      //margin-bottom: 211px;
      line-height: 72px;
      font-size: 72px;
    }
    span {
      display: block;
      @include respond-to(md) {
        display: inline;
      }
    }
  }

  &__nav {
    margin: 311px auto 0;
    display: none;
    align-items: center;
    justify-content: space-between;
    max-width: 500px;
    @include respond-to(xl) {
      max-width: 1020px;
    }
    @include respond-to(lg) {
      max-width: 760px;
    }
    @include respond-to(md) {
      display: flex;
      max-width: 580px;
    }
    
    img {
      cursor: pointer;

      &:hover {

      }
    }

    &--hover {
      display: none;
    }
  }

  &__icon-image {
    width: 40px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    cursor: pointer;

    &:before {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background: transparent;
      z-index: 10;
    }
    &:hover {
      .banner__nav--static {
        display: none;
      }

      .banner__nav--hover {
        display: block;
      }
    }

    img {
      z-index: 9;
    }
  }
}

@keyframes bannerImg {
   0% { transform: scale(1); }
  20% {
    transform: scale(1.1);
  }
  40% {
    transform: scale(1.2);
  }
  60% {
    transform: scale(1.3);
  }
  80% {
    transform: scale(1.4);
  }
  100% {
    transform: scale(1.5);
  }
  //60% { transform: scale(1.6); }
  //70% { transform: scale(1.7); }
  //80% { transform: scale(1.8); }
  //90% { transform: scale(1.9); }
  //100% { transform: scale(2); }
}