.coctail-detail {
  padding-top: 30px;
  .content-wrap {
    align-items: flex-start;
    flex-direction: column;
    position: relative;

    @include respond-to(md) {
      padding-top: 40px;
      flex-direction: row;
      display: grid;
      grid-template-columns: 30% 70%;
    }
    @include respond-to(lg) {
      padding-top: 72px;
      display: grid;
      grid-template-columns: 160px auto;
      grid-gap: 60px;
    }
    @include respond-to(xl) {
      grid-gap: 190px;
    }
  }

}

aside {
  order: 12;
  @include respond-to(md) {
    order: 0;
  }
}
.sidebar {
  margin: 0 auto;
  grid-column: 1/2;
  order: 12;
  @include respond-to(md) {
    margin: 0 50px 0 0;
    order: 0;
    max-width: 162px;
  }
  @include respond-to(lg) {
    margin: 0;
  }
}

.coctail {
  grid-column: 2/3;
}