.news {
  padding: 80px 0 100px;

  .content-wrap {
    justify-content: center;
  }
  &__wrap {
    align-items: flex-start;

    &--desktop {
      display: none;
      @include respond-to(md) {
        display: flex;
      }
    }
    &--mobile {
      display: block;
      width: 100%;
      max-width: 290px;
      @include respond-to(md) {
        display: none;
      }
      @include respond-to(sm) {
        max-width: 80%;
      }
    }
  }
  &__info {
    padding-right: 40px;
    @include respond-to(lg) {
      width: 50%;
    }
    @include respond-to(md) {
      width: 60%;
    }
  }
  .news-item {
    margin: 0 auto 50px;
    border-bottom: 1px solid #8c8c8c;
    max-width: 290px;
    @include respond-to(lg) {
      width: 100%;
      max-width: 435px;
    }
    @include respond-to(md) {
      border-color: $gray;
    }
    @include respond-to(sm) {
      max-width: 80%;
    }

    &__date {
      margin-bottom: 14px;
      color: #959595;
      font-weight: 400;
      font-size: 11px;
      font-family: $subFont;
      text-align: center;
      @include respond-to(lg) {
        font-size: 12px;
      }
      @include respond-to(md) {
        text-align: left;

      }
    }
    &__name {
      margin-bottom: 12px;
      font-weight: normal;
      font-size: 14px;
      color: $black;
      text-align: center;
      @include respond-to(lg) {
        font-size: 24px;
      }
      @include respond-to(md) {
        text-align: left;
      }

      a {
        color: $dark;
      }
    }
    &__preview {
      margin-bottom: 30px;
      font-weight: 400;
      font-size: 13px;
      line-height: 21px;
      font-family: $subFont;
      color: #878787;
      text-align: center;
      @include respond-to(lg) {
        font-size: 15px;
      }
      @include respond-to(md) {
        text-align: left;

      }
    }
  }


  &__pictures {
    margin: 0 auto 20px;
    //max-width: 290px;
    @include respond-to(lg) {
      margin: 0;
      max-width: 50%;
    }
    @include respond-to(md) {
      max-width: 40%;
    }
    @include respond-to(sm) {
      //max-width: 80%;
    }
  }

  &__description {
    margin: 0 auto;
    //max-width: 290px;
    @include respond-to(md) {
      max-width: 50%;
    }
  }
}

.news__wrap--mobile {
  position: relative;
  button.slick-arrow {
    margin: 0;
    display: block!important;
    background: transparent;
    width: 25px;
    height: 26px;
    border: none;
    background: transparent url("/images/icons/slider-arrow.png") center no-repeat;
    background-size: cover;

    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 10;
    cursor: pointer;
    outline: none;

    &:focus {
      outline: none;
    }
  }
  .slick-prev {
    left: 0;
  }
  .slick-next {
    left: calc(100% - 25px);
    transform: rotate(180deg);
  }
}