.products {
  margin: 0 auto;
  padding: 60px 0;
  .content-wrap {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    justify-content: space-between;
    @include respond-to(md) {
      flex-wrap: nowrap;
    }
  }
}

.product-item {
  padding: 0 7px;
  @include respond-to(md) {
    padding: 0 14px;
  }

  &:hover {
    .product-item {
      &__picture {
        &:before {
          display: block;
        }

        img {
          transform: scale(1.1);
          transition: transform .4s;
        }
      }

      &__name {
        color: #646262;
      }
    }
  }

  &--big {
    margin-bottom: 30px;
    width: 100%;
    @include respond-to(md) {
      padding-left: 0;
      margin-bottom: 0;
      width: 45%;
    }


  }
  &__picture {
    margin-bottom: 25px;
    display: flex;
    align-items: center;
    overflow: hidden;
    justify-content: center;
    position: relative;
    cursor: pointer;
    @include respond-to(md) {
      justify-content: flex-start;
    }

    a {
      min-width: 100%;

      img {
        min-width: 100%;
        transition: transform 1s;
      }
    }

    &:before {
      content: '';
      display: none;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: rgba(0, 0, 0, .05);
      z-index: 100;
    }
  }

  &__place {
    margin-bottom: 0;
    color: #8a8a8a;
    font-size: 11px;
    font-weight: 400;
    line-height: 30px;
    text-align: center;
    @include respond-to(md) {
      font-size: 15px;
      text-align: left;
    }
  }
  &__name {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 18px;
    color: $dark;
    text-align: center;
    @include respond-to(md) {
      text-align: left;
      max-width: 460px;
      max-width: 80%;
      font-size: 36px;
    }
  }
  &__price {
    display: flex;
    align-items: center;
    justify-content: center;
    @include respond-to(md) {
      justify-content: flex-start;
    }
    p {
      margin-right: 16px;
      display: inline-block;
      font-size: 15px;
      font-weight: 500;
      font-family: $subFont;
    }
  }


  &--small {
    padding: 0;
    display: flex;
    align-items: flex-start;
    align-self: stretch;
    width: 100%;
    justify-content: center;
    @include respond-to(md) {
      justify-content: flex-end;
      padding: 0 0 0 14px;
      width: 50%;
    }

    .product-item {
      width: 50%;
      max-width: 50%;
      max-width: 286px;
      &:last-of-type {
        @include respond-to(md) {
          padding-right: 0;
          align-self: flex-end;
        }
      }
      &__picture {
        //background: $gray-light;
        height: 140px;
        overflow: hidden;
        @include respond-to(md) {
          height: auto;
          a {
            width: 100%;

            img {
              width: 100%;
            }
          }
        }
        @include respond-to(sm) {
          height: 210px;
        }
      }
      &__place {
        margin-bottom: 4px;
        font-size: 13px;
        text-align: left;

        a {
          color: $gray-dark;
          font-family: 'Roboto', sans-serif;
        }
      }
      &__name {
        margin-top: 0;
        margin-bottom: 8px;
        font-size: 16px;
        text-align: left;
      }
      &__price {
        justify-content: flex-start;
      }


    }
  }
}