.places {
  padding: 50px 0 70px;
  &__list {}
  .container {
    width: 100%;
  }

  .content-wrap {
    padding: 0 8px;
    @include respond-to(sm) {
      padding: 0 20px;
    }
  }
}

.places-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 100%;

  &__item {
    //margin: 0 8px 15px;
    margin-bottom: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 570px;
    overflow: hidden;
    position: relative;

    padding: 0 3px;
    height: 140px;
    @include respond-to(xl) {
      //height: 296px;
      height: 315px;
    }
    @include respond-to(lg) {
      //height: 190px;
      height: 260px;
    }
    @include respond-to(md) {
      //height: 160px;
    }
    @include respond-to(sm) {
      padding: 0 8px;
      margin-bottom: 15px;
      //height: 250px;
      height: 210px;
    }

    &:hover {
      .places-list__address {
        opacity: 1;
      }
    }

    &--large {
      width: 100%;
      max-width: 100%;
      @include respond-to(sm) {
        max-width: 50%;
      }
      //@include respond-to(xl) {
      //  width: 500px;
      //  max-width: 100%;
      //}
      //@include respond-to(lg) {
      //  max-width: 390px;
      //  width: 440px;
      //}
      //@include respond-to(md) {
      //  width: 330px;
      //}
      //@include respond-to(sm) {
      //  width: 510px;
      //}
      &:nth-of-type(2) {
        @include respond-from(md) {
          order: 12;
        }
      }
    }
    &--small {
      width: 100%;
      max-width: 50%;
      @include respond-to(sm) {
        max-width: 25%;
      }

    }
  }
  &__img-wrap {
    min-height: 100%;
    min-width: 100%;
    position: relative;
    overflow: hidden;
    img {
      min-width: 100%;
      min-height: 100%;
      max-width: none;
    }
  }

  &__address {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: rgba(0,0,0,.5);
    transition: all .4s;
    opacity: 0;
  }
  &__name {
    margin-bottom: 10px;
    font-size: 24px;
    font-weight: 700;
    line-height: 20px;
    color: $white;
  }
  &__geo {
    font-size: 13px;
    font-weight: 400;
    line-height: 20px;
    font-family: $subFont;
    color: $dark-gray;
  }
}