.feedback {
  padding: 58px 10px;
  background: $beige;

  &__form {
    margin: 0 auto;
    padding: 67px 20px;
    max-width: 810px;
    border: 4px solid $white;
    text-align: center;

    h2 {
      margin-bottom: 35px;
      font-size: 24px;
      font-weight: 700;
    }
  }

  form {
    .group-item {
      margin: 0 auto;
      position: relative;
      @include respond-to(sm) {
        max-width: 70%;
      }
      @include respond-to(md) {
        max-width: 80%;
      }
      @include respond-to(xl) {
        width: 419px;
      }

      &--input {
        label {
          margin: 0;
          width: 60px;
          font-size: 11px;
          font-weight: 400;
          line-height: 24px;
          text-transform: uppercase;
          letter-spacing: 0.55px;
          text-align: center;
          @include respond-to(sm) {
            text-align: right;
            position: absolute;
            left: -80px;
            top: 40%;
            transform: translateY(-50%);
          }
        }
      }

      &--checkbox {
        text-align: left;
      }
    }

    label {
      color: #878787;
      font-size: 12px;
      font-weight: 400;
      line-height: 24px;
      text-transform: uppercase;
      letter-spacing: 0.6px;
    }

    input[type="text"] {
      padding: 16px 15px;
      margin-bottom: 19px;
      font-size: 14px;
      font-weight: 400;
      line-height: 24px;
      border: 1px solid $gray;
      width: 100%;

      &:hover, &:active {
        //border-color: $black;
        box-shadow: $defaultShadow;
      }
    }

    .submit-btn {
      margin-top: 25px;
      padding: 12px 62px;
      font-size: 12px;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.9px;
      color: $pink;
      border: 2px solid $pink;
      border-radius: 3px;
      background: transparent;

      &:hover {
        background: $pink;
        color: $beige;
      }
    }
  }

  .group-item--checkbox {
    input {
      display: none;

      &:checked + label::before {
        background: $white url("/images/icons/checkbox-arrow.svg") center no-repeat;
      }
    }

    label {
      font-size: 13px;
      font-weight: 400;
      line-height: 21px;
      text-transform: none;
      color: #878787;
      cursor: pointer;

      a {
        color: #878787;
        text-decoration: underline;

        &:hover {
          color: $pink;
          text-decoration: none;
        }
      }

      &:before {
        content: '';
        display: inline-block;
        margin-right: 9px;
        width: 13px;
        height: 13px;
        border: 1px solid #cacaca;
        background: $white;
        position: relative;
        top: 2px;
      }
    }
  }
}