.footer {
  .content-wrap {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    @include respond-to(md) {
      justify-content: space-between;
    }
  }

  &__top {
    padding: 44px 0 30px;
    border-top: 1px solid #e3e3e3;
  }
  &__nav {
    display: none;
    align-items: flex-start;
    justify-content: space-between;
    width: 75%;
    @include respond-to(md) {
      display: flex;
    }

    ul {
      li {
        margin-bottom: 8px;
        a {
          font-size: 13px;
          font-weight: 400;
          line-height: 18px;
          color: $dark;
          font-family: $subFont;

          &:hover {
            color: $pink;
          }
        }
      }
    }
  }


  &__bot {
    padding: 30px 0;
    border-top: 1px solid #e3e3e3;
  }
  &__bot-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    a {
      color: $dark;
      font-family: $subFont;
      font-size: 13px;
      font-weight: 400;
      line-height: 18px;
      &:hover {
        color: $pink;
      }
    }
  }
  &__date {
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    font-family: $subFont;
    color: $dark;
  }
  &__policy {
    display: none;
    @include respond-to(md) {
      display: block;
    }
    a:first-of-type {
      margin-right: 50px;
    }
  }
  &__madein {
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    color: $dark;
    font-family: $subFont;
    a {
      font-weight: 500;
    }
  }
}

.footer-contacts {
  &__city {
    margin-bottom: 20px;
    text-align: center;
    @include respond-to(md) {
      text-align: left;
    }
    a {
      font-size: 13px;
      font-weight: 400;
      line-height: 24px;
      color: $pink;
      font-family: $subFont;
      border-bottom: 1px dotted $pink;
    }
  }
  &__phone {
    margin-bottom: 30px;
    text-align: center;
    @include respond-to(md) {
      margin-bottom: 40px;
      text-align: left;
    }
    a {
      font-size: 18px;
      font-weight: 700;
      line-height: 18px;
      color: $dark;
      @include respond-to(lg) {
        font-size: 24px;
      }
      @include respond-to(md) {
        font-size: 23px;
      }
    }
  }
  &__social {}

  &__address {
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    color: $dark;
    text-align: center;
    font-family: $subFont;
    @include respond-to(md) {
      max-width: 130px;
      text-align: left;
    }
  }
}
.footer-social {
  margin-bottom: 30px;
  @include respond-to(md) {
    margin-bottom: 50px;
  }
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    @include respond-to(md) {
      justify-content: flex-start;
    }
    &:first-of-type {
      margin-bottom: 15px;
    }

    a {
      font-size: 12px;
      font-weight: 400;
      line-height: 18px;
      color: $dark;
      font-family: 'Roboto', sans-serif;
      @include respond-to(md) {
        font-size: 14px;
      }
      &:before {
        content: '';
        display: inline-block;
        margin-right: 15px;
        width: 15px;
        min-width: 15px;
        height: 15px;
        background-size: contain;
        position: relative;
        top: 3px;
      }
    }
  }
  &__fb:before {
    background: url("/images/icons/fb-footer.svg") center no-repeat;
  }
  &__inst:before {
    background: url("/images/icons/inst-footer.svg") center no-repeat;
  }
}