.favorite-container {
  width: 100%;
  max-width: 1450px;
}

.favorite {
  padding: 70px 0 110px;

  .content-wrap {
    &:last-child {
      @include respond-from(sm) {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }

  h1 {
    margin-bottom: 30px;
    font-size: 42px;
    font-weight: 700;
    line-height: 1.44;
    text-align: left;
  }

  &__filter-block {
    margin-bottom: 38px;
    display: flex;
    align-items: center;
    justify-content: flex-start;

    p {
      margin-right: 15px;
      font-size: 11px;
      font-weight: 400;
      line-height: 1.44;
      text-transform: uppercase;
      letter-spacing: 1.1px;
    }
  }

  &__filter {
    position: relative;

    a {
      display: inline-block;
      padding: 10px 15px;
      font-size: 14px;
      font-weight: 400;
      line-height: 24px;
      width: 130px;
      border: 1px solid $gray;
      background: url("/images/icons/arrow-filter.png") 90% center no-repeat;

      &:hover {
        color: $black;
      }
    }
  }

  &__dropdown {
    display: none;
    margin: 0;
    padding: 0;
    width: 100%;
    position: absolute;
    top: 100%;
    left: 0;
    border: 1px solid $gray;

    li {
      padding: 8px;
      margin: 0;
      cursor: pointer;

      &:hover {
        background: $gray-light;
      }
    }
  }

  &__table {
    width: 100%;
  }


  &__total {
    padding: 35px 0;
    border: 2px solid $gray;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;

    @include respond-to(md) {
      flex-direction: row;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    width: 100%;
    @include respond-to(md) {
      padding-right: 25px;
      flex-direction: row;
    }
    @include respond-to(lg) {
      max-width: calc(100% - 490px);
    }
    @include respond-to(xl) {
      max-width: calc(100% - 580px);
    }

    p {
      padding: 14px 0;
      border: none;
      @include respond-to(md) {
        border-bottom: 1px dashed;
        padding: 0;
      }
    }
  }

  &__all {
    padding: 0 25px;
    font-size: 18px;
    font-weight: 400;
    @include respond-to(lg) {
      width: 490px;
    }
    @include respond-to(xl) {
      width: 580px;
    }

    span {
      font-weight: 500;
      color: $pink;
    }
  }

  &__save, &__back, &__clear {
    display: inline-block;
    font-size: 12px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    line-height: 16px;
    border-bottom: 1px dashed;
    position: relative;

    &:hover {
      border-bottom: 1px solid;
    }

    &:before {
      content: '';
      display: inline-block;
      width: 16px;
      height: 14px;
      position: absolute;
      left: -20px;
      top: 15px;
      @include respond-to(md) {
        top: 1px;
      }
    }
  }

  &__save {
    color: $pink;
    @include respond-to(md) {
      margin-left: 26px;
    }

    &:before {
      left: -30px;
      background: url("/images/icons/save.svg") center no-repeat;
    }
  }

  &__back {
    &:before {
      background: url("/images/icons/price-arrow.svg") center no-repeat;
      background-size: contain;
      transform: rotate(-90deg);
    }
  }

  &__clear {
    &:before {
      background: url("/images/icons/close.svg") center no-repeat;
    }
  }
}

.favorite-table {
  border-top: 1px solid $gray;
  @include respond-to(sm) {
    border: none;
  }

  tr {
    display: flex;
    flex-wrap: wrap;
    @include respond-to(sm) {
      display: table-row;
    }

    &:first-child {
      border-bottom: 1px solid $black;
      display: none;
      @include respond-to(sm) {
        display: table-row;
      }

      th {
        padding: 12px 0;

        &:first-child {
          @include respond-to(sm) {
            width: 220px;
          }
          @include respond-to(md) {
            width: 360px;
          }
          @include respond-to(lg) {
            width: 490px;
          }
          @include respond-to(xl) {
            width: 580px;
          }
        }
      }
    }

    &:not(:first-child) {
      td {
        padding: 23px 0;
      }
    }

    td {
      &:first-child {
        width: 100%;
        justify-content: center;
        @include respond-to(sm) {
          justify-content: flex-start;
          width: auto;
          display: flex;
        }
      }

      &:not(:first-child) {
        display: inline-block;
        width: calc(100% / 3);
        text-align: center;
        border-top: 1px solid $gray;
        border-bottom: 1px solid $gray;
        @include respond-to(sm) {
          border: none;
          display: table-cell;
          width: auto;
          text-align: left;
        }
      }

      &:nth-last-child(2), &:nth-last-child(4) {
        div {
          line-height: 48px;
          @include respond-to(sm) {
            line-height: inherit;
          }
        }
      }

      &.favorite-table__remove-cell {
        display: none;
        @include respond-to(sm) {
          display: table-cell;
        }
      }

      &.favorite-table__header-mobile {
        padding: 10px 0 8px;
        border: none;
        display: flex;
        width: 100%;
        @include respond-to(sm) {
          display: none;
        }

        p {
          display: inline-block;
          width: calc(100% / 3);
          font-size: 11px;
          font-weight: 400;
          color: #494949;
        }
      }
    }

    th {
      font-size: 13px;
      font-weight: 400;
      line-height: 30px;
    }
  }

  &__image {
    padding: 0 18px;
  }

  &__info {
    max-width: 255px;

    p {
      font-size: 13px;
      font-weight: 400;
      color: #878787;
      font-family: $subFont;

      &.favorite-table__name {
        margin-bottom: 2px;
        font-size: 18px;
        font-weight: 500;
        color: $black;
      }
    }
  }

  &__product {
    a {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      max-width: 580px;
    }
  }

  &__price {
    font-size: 18px;
    font-weight: 500;
  }

  &__sum {
    font-size: 18px;
    font-weight: 500;

    span {
      display: inline-block;
      width: 60px;
    }
  }

  &__counter {
    margin: 0 auto;
    width: 120px;
    justify-content: center;
    @include respond-to(sm) {
      justify-content: flex-start;
      margin: 0;
    }
  }

  &__remove {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url("/images/icons/remove.svg") center no-repeat;
    background-size: contain;
    cursor: pointer;
  }


}

.counter-product {
  display: flex;
  align-items: center;

  input {
    padding: 12px 0;
    font-size: 16px;
    text-align: center;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 1.6px;
    width: 70px;
    border: 1px solid #e3e3e3;
    border-right: none;
    border-left: none;
  }

  &__change {
    padding: 0 11px;
    height: 48px;
    display: inline-block;
    width: 32%;
    border: 1px solid #e3e3e3;
  }

  &__minus {
    background: url("/images/icons/minus_default.svg") center no-repeat;
    border-right: none;
    border-radius: 3px 0 0 3px;
    cursor: pointer;
  }

  &__plus {
    background: url("/images/icons/plus.svg") center no-repeat;
    border-left: none;
    border-radius: 0 3px 3px 0;
    cursor: pointer;
  }
}