.coctail {
  margin-bottom: 30px;
  max-width: 100%;
  @include respond-to(md) {
    margin-bottom: 90px;
    width: 805px;
    //max-width: calc(100% - 210px);
    max-width: 100%;
  }
  @include respond-to(lg) {
    max-width: 730px;
  }
  @include respond-to(xl) {
    max-width: 100%;
  }

  &__name {
    margin-bottom: 8px;
    text-align: center;
    font-size: 24px;
    font-weight: 700;
    line-height: 1.44;
    @include respond-to(md) {
      margin-bottom: 30px;
      font-size: 42px;
      text-align: left;
    }
  }


  &__description {
    margin: 0 auto 38px;
    max-width: 585px;
    font-size: 15px;
    font-weight: 400;
    line-height: 22px;
    @include respond-to(md) {
      margin: 0 auto 56px;
      font-size: 18px;
      line-height: 26px;
    }
  }

  &__ingridient {
    padding-top: 30px;
    border-top: 1px solid $gray;
    @include respond-to(md) {
      padding-top: 59px;
    }

    h2 {
      margin-bottom: 35px;
      font-size: 18px;
      font-weight: 700;
      line-height: 26px;
      @include respond-to(md) {
        font-size: 24px;
      }
    }
  }

  &__composition {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include respond-to(md) {
      align-items: flex-start;
      justify-content: space-between;
      flex-direction: row;
    }
  }

  &__list {
    margin-bottom: 40px;
    width: 100%;
    max-width: 350px;
    @include respond-to(md) {
      margin-bottom: 0;
    }
  }

  &__recipe {
    width: 100%;
    max-width: 350px;

    p {
      margin-bottom: 20px;
      font-family: "PT Serif";
      font-size: 16px;
      font-weight: 400;
      line-height: 22px;
    }
  }
}

.coctail-list {
  margin: 0 0 30px;
  @include respond-to(md) {
    margin: 0 30px 0 0;
  }

  &__item {
    margin: 0;
    padding: 12px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid $gray;
    max-width: 100%;
  }

  &__name {
    margin-right: 20px;
  }

  &__quantity {
    white-space: nowrap;
  }
}

.coctail-detail__counter {
}

.detail-slider {
  .detail-item {
    &__image {
      padding: 20px 0 53px;
      background: $beige;

      img {
        margin: 0 auto;
      }
    }
  }

  button.slick-arrow {
    background: transparent;
    width: 25px;
    height: 26px;
    border: none;
    background: transparent url("/images/icons/slider-arrow.svg") center no-repeat;
    background-size: contain;

    position: absolute;
    bottom: -60px;
    right: 0;
    z-index: 10;
    cursor: pointer;
    outline: none;

    &:active {
      outline: none;
      border: none;
    }

    &.slick-next {
      display: block !important;
      transform: rotate(180deg);
      left: calc(103% + 16px);
      //@include respond-to(lg) {
      //  left: 100%;
      //}
      @include respond-to(md) {
        left: 103%;
      }
      //@include respond-to(sm) {
      //}
    }

    &.slick-prev {
      display: block !important;
      left: 56px;
      @include respond-to(lg) {
        left: 97%;
      }
      @include respond-to(md) {
        left: 97%;
      }
      @include respond-to(sm) {
      }
    }
  }

  &__counter {
    margin: 20px 0 36px;
    padding: 14px 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid $black;
    @include respond-to(md) {
      margin: 0 0 60px;
      padding: 32px 0 28px;
      border-bottom: 2px solid $black;
      border-top: none;
      justify-content: flex-start;
    }

    p {
      color: #878787;
      font-size: 14px;
      font-weight: 500;
      line-height: 24px;
      letter-spacing: 2px;

      &.detail-slider__all {
        color: $black;
      }
    }
  }
}