.product-slider {
  padding: 30px 0 60px;
  .container {
  }
  .content-wrap {
    padding: 0 !important;
    max-width: 100%;
    @include respond-to(xl) {
      max-width: 1140px;
    }
    @include respond-to(lg) {
      max-width: 960px;
    }
    @include respond-to(md) {
      max-width: 720px;
    }
  }
}
.slider {
  margin: 0 auto;
  position: relative;
  max-width: 100%;
  @include respond-to(lg) {
    max-width: 900px;
  }

  button.slick-arrow {
    background: transparent;
    width:30px;
    height: 30px;
    border: none;
    background: transparent url("/images/icons/slider-arrow.svg") center no-repeat;
    background-size: contain;

    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 10;
    cursor: pointer;
    outline: none;

    &:active {
      outline: none;
      border: none;
    }

    &.slick-next {
      display: block !important;
      transform: rotate(180deg);
      left: 100%;
      @include respond-to(lg) {
        left: 86%;
      }
      @include respond-to(md) {
        left: 95%;
      }
      @include respond-to(sm) {
      }
    }
    &.slick-prev {
      display: block !important;
      left: 70px;
      @include respond-to(lg) {
        left: 81%;
      }
      @include respond-to(md) {
        left: 88%;
      }
      @include respond-to(sm) {
      }
    }
  }
}
.slider-item {
  padding: 30px 15px;
  display: flex !important;
  align-items: flex-start ;
  flex-wrap: wrap;
  background: $beige;
  position: relative;
  @include respond-to(md) {
    flex-wrap: nowrap;
  }

  &.slick-active {
    position: relative;
  }

  &__new {
    position: absolute;
    background: transparent url("/images/del/new-icon.png") center no-repeat;
    background-size: contain;
    width: 42px;
    min-width: 42px;
    height: 42px;
    top: 15px;
    right: 15px;
    @include respond-to(md) {
      top: 40px;
      right: 50px;
      width: 55px;
      min-width: 55px;
      height: 55px;
    }
    @include respond-to(sm) {
      top: 30px;
      right: 40px;
      width: 45px;
      min-width: 45px;
      height: 45px;
    }
  }
  &__info {
    margin-top: 10px;
    width: 100%;
    @include respond-to(md) {
      margin-top: 90px;
      max-width: 300px;
    }
  }

  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-width: 100%;
    overflow: hidden;
    max-height: 300px;
    @include respond-to(md) {
      width: 50%;
      min-width: 50%;
      max-height: none;
    }

    img {
      max-height: 300px;
      @include respond-to(md) {
        max-height: none;
      }
    }
  }

  &__geo {
    margin-bottom: 10px;
    font-size: 11px;
    font-weight: 400;
    line-height: 30px;
    color: $gray-dark;
    text-align: center;
    @include respond-to(md) {
      font-size: 15px;
      text-align: left;
    }

    a {
      color: $gray-dark;
      font-family: 'Roboto', sans-serif;
    }
  }

  &__drink {
    color: #9c3862;
    font-size: 10px;
    font-weight: 400;
    line-height: 72px;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-family: $mainFont;
    text-align: center;
    @include respond-to(md) {
      text-align: left;
    }

    a {
      color: $pink;
    }
  }
  &__name {
    margin-bottom: 20px;
    font-size: 18px;
    font-weight: 700;
    text-align: center;
    @include respond-to(md) {
      text-align: left;
      font-size: 36px;
    }
  }
  &__descr {
    font-size: 15px;
    font-weight: 400;
    line-height: 21px;
    font-family: "PT Serif";
    text-align: center;
    color: $dark;
    @include respond-to(md) {
      text-align: left;
    }
  }

  &__price {
    display: flex;
    align-items: center;
    justify-content: center;
    @include respond-to(md) {
      justify-content: flex-start;
    }
    p {
      margin-right: 16px;
      display: inline-block;
      font-size: 13px;
      font-weight: 500;
      font-family: $subFont;
      @include respond-to(md) {
        font-size: 15px;
      }
    }
  }

  &__nav {
    margin-top: 47px;
    padding-top: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid $black;
    @include respond-to(md) {
      margin-top: 150px;
      justify-content: flex-start;
    }
    p {
      display: inline-block;
      font-size: 14px;
      font-weight: 500;
      line-height: 24px;
      font-family: $subFont;
      color: $gray-dark;
      letter-spacing: 6px;
      text-align: center;
      @include respond-to(md) {
        text-align: left;
      }
    }
  }
  &__now {
    color: $black !important;
  }
}


.coctail-slider {
  padding: 30px 0 70px;
  .content-wrap {
    padding: 0 !important;
    max-width: 100%;
    @include respond-to(xl) {
      max-width: 1140px;
    }
    @include respond-to(lg) {
      max-width: 960px;
    }
    @include respond-to(md) {
      max-width: 720px;
    }
  }
  .slider-item {
    padding-bottom: 70px;
    &__nav {
      @include respond-to(md) {
        margin-top: 60px;
      }
    }
  }
}