.brands {
  //margin: 0 30px;
  padding: 77px 0 0;

  .content-wrap {
    display: block;
    padding: 0;
    @include respond-to(lg) {
      padding: 0 20px;
    }
  }

  h2 {
    margin-bottom: 25px;
    font-size: 24px;
    font-weight: 700;
    line-height: 72px;
    text-align: center;
  }
  &__list {}

  &__more {
    margin-top: 20px;
    text-align: center;
    @include respond-to(md) {
      margin-top: 40px;
    }
    a {
      font-size: 12px;
      line-height: 42px;
      font-weight: bold;
      text-transform: uppercase;
      letter-spacing: 0.9px;
      position: relative;
      @include respond-to(md) {
        line-height: 72px;
      }
      &:after {
        content: '';
        display: inline-block;
        margin-left: 13px;
        width: 12px;
        height: 7px;
        background: url("/images/icons/arrow-more.svg") center no-repeat;
        background-size: contain;
        position: absolute;
        right: -20px;
        top: 3px;
        transition: right .4s;
      }

      &:hover {
        color: #646262;

        &:after {
          right: -26px;
        }
      }
    }
  }
}

.brand-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  overflow: hidden;

  max-width: 1500px;
  margin: 0 auto;
  ul {
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
    position: relative;
    transition: all .8s;
    @include respond-to(lg) {
      width: 100%;
      position: static;
      flex-wrap: wrap;
    }
  }
  &__item {
    padding: 0;
    width: 136px;
    height: 120px;
    border: 1px solid $gray;

    @include respond-to(sm) {
      width: 146px;
      height: 130px;
    }
    @include respond-to(md) {
      width: 246px;
      height: 220px;
    }
    @include respond-to(lg) {
      width: 20%;
    }

    &:not(:last-child) {
      border-right: none;
      @include respond-to(lg) {
        border-right: 1px solid $gray;
      }
    }

    &:not(:nth-child(5n)) {
      @include respond-to(lg) {
        border-right: none;
      }
    }

    &:nth-child(n+6) {
      @include respond-to(lg) {
        border-top: none;
      }
    }

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      height: 100%;

      &:hover {
        background: $gray-light;
      }
    }

    img {
      max-width: 80%;
    }
  }
}