.header {
  transition: top 1s;
  &__top {
    //padding-left: 30px;
    border-bottom: 1px solid $gray;

    display: none;
    @include respond-to(lg) {
      display: block;
    }
  }

  &__city {
    @include respond-to(xl) {
      //width: 16.666667%;
      width: 420px;
    }
    @include respond-to(lg) {
      width: 12%;
    }
    a {
      display: inline-block;
      white-space: nowrap;
      font-size: 13px;
      font-weight: 400;
      line-height: 18px;
      font-family: $subFont;

      &:hover {
        color: #646262;
        border-bottom: none;
      }
    }
  }
  &__top-nav {
    padding: 10px 0;
    @include respond-to(xl) {
      width: 50%;
    }
    @include respond-to(md) {
      width: 54%;
    }
    ul {
      margin: 0;
      li {
        padding-left: 5%;
        padding-bottom: 0;
        display: inline-block;
        @include respond-to(xl) {
          padding-left: 4%;
        }

        &:first-child {
          @include respond-to(xl) {
            padding-left: 0;
          }
        }
        a {
          font-size: 13px;
          font-weight: 400;
          line-height: 24px;
          font-family: $subFont;
        }
      }

      &:hover {
        a {
          color: #646262;
        }
      }
    }
  }

  &__info {
    //flex: 2;
    padding-right: 25px;
    width: 25%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-family: $subFont;
  }
  &__phone {
    margin-right: 12px;
    font-size: 12px;
    line-height: 24px;
    color: $gray;
    white-space: nowrap;
    span {
      font-weight: bold;
      font-size: 16px;
    }
  }
  &__work {
    a {
      font-size: 11px;
      font-weight: 400;
      line-height: 24px;

      &:hover {
        color: #646262;
        border-bottom: none;
      }
    }
  }


  &__bot {
    padding: 12px 10px;
    //transition: top 1s;
    border-bottom: 1px solid $gray;
    transition: opacity .8s;
    @include respond-to(md) {
      padding: 20px 0 20px 0;
    }
    .content-wrap {
      justify-content: space-between;
      @include respond-to(md) {
        justify-content: flex-start;
      }

    }
    &.sticky {
      position: fixed;
      top: 0;
      width: 100%;
      background: $white;
      z-index: 100;
    }
  }

  &__logo {
    order: 10;
    cursor: pointer;
    @include respond-to(xl) {
      //width: 16.666666%;
      width: 370px;
    }
    @include respond-to(md) {
      width: 12%;
      order: 0;
    }

    &:hover {
      .header__logo--static {
        display: none;
      }

      .header__logo--hover {
        display: block;
      }
    }

    &--hover {
      display: none;
    }

    img {
      max-width: 80px;
      @include respond-to(sm) {
        max-width: 100%;
      }
    }
  }

  &__submenu {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background: $white;
    z-index: 100;
    display: none;
  }
  &__bot-nav {
    display: none;
    @include respond-to(xl) {
      width: 50%;
    }
    @include respond-to(md) {
      display: block;
      width: 54%;
    }

    ul.header__first-menu {
      margin: 0;

      & > li {
        position: relative;
        padding-left: 3%;
        display: inline-block;

        &:not(:first-of-type) {
          @include respond-to(lg) {
            padding-left: 8%;
          }
          @include respond-to(xl) {
            padding-left: 10%;
          }
        }

        &:first-of-type {
          &:after {
            content: '';
            display: inline-block;
            margin-left: 8px;
            width: 21px;
            height: 21px;
            background: url("/images/icons/arrow-down.svg") center no-repeat;
            //background-size: 60%;
            //border: 1px solid #cacaca;
            border-radius: 50%;
            position: relative;
            top: 5px;
            cursor: pointer;
            background-size: contain;
          }
        }

        &:hover {
          .header__submenu {
            display: block;
          }
        }

        &:first-child {
          @include respond-to(xl) {
            padding-left: 0;
          }
        } 
        a {
          font-size: 18px;
          font-weight: 700;
          line-height: 30px;
          color: $black;

          &:hover {
            color: #646262 !important;
          }
        }
      }
    }
  }
  &__search {
    display: none;
    text-align: right;
    @include respond-to(lg) {
      width: 19%;
    }
    @include respond-to(md) {
      width: 25%;
      display: block;
    }
    input {
      padding: 10px 0 10px 14px;
      width: 100%;
      max-width: 310px;
      font-size: 13px;
      font-weight: 400;
      line-height: 24px;
      background: url("/images/icons/search.png") 97% center no-repeat;
      border: 1px solid $gray;
      font-family: $subFont;

      &:hover, &:active, &:focus {
        background: url("/images/icons/search_hover.svg") 97% center no-repeat;
        border-color: $gray-dark;
      }
    }
  }

  &__likes {
    order: 0;
    position: relative;
    @include respond-to(xl) {
      min-width: 70px;
    }

    &:hover {
      .header__likes--static {
        display: none;
      }

      .header__likes--hover {
        display: block;
      }

      p {
        display: inline-block;
        //position: absolute;
        //right: -16px;
        //top: -4px;
        color: $pink;
        @include respond-to(md) {
          position: static;
        }
      }
    }

    &--hover {
      display: none;
    }

    a {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 100%;
    }
    @include respond-to(md) {
      flex: 1;
      order: 12;
    }

    img {
      margin-right: 10px;
    }
    p {
      font-size: 13px;
      font-weight: 400;
      line-height: 24px;
      color: #b8b8b8;
      font-family: $subFont;
    }
  }

  &__menu {
    order: 12;
    display: block;
    @include respond-to(md) {
      display: none;
    }
  }
  &__menu-btn {
    span {
      display: inline-block;
      width: 15px;
      height: 2px;
      background: $black;
      position: relative;
      &:after {
        top: -4px;
      }
      &:before {
        top: 4px;
      }
    }
    span:after, span:before {
      content: '';
      display: inline-block;
      width: 15px;
      height: 2px;
      background: $black;
      position: absolute;
      left: 0;
    }
  }
}

.header-social {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  align-self: stretch;
  width: 9%;
  &__link {
    width: 34px;
    min-width: 34px;
    height: 34px;
    min-height: 34px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: stretch;
    border-left: 1px solid $gray;
    &:last-of-type {
      border-right: 1px solid $gray;
    }

    &:hover {
      background: #f8f8f8;
    }

    @include respond-to(lg) {
      width: 54px;
      min-width: 54px;
      height: 54px;
      min-height: 54px;
    }
  }
}