.other-coctail {
  padding: 60px 0 50px;
  border-top: 1px solid #e3e3e3;
  @include respond-to(md) {
    padding: 60px 0 100px;
  }

  .content-wrap {
    flex-direction: column;
  }

  h2 {
    margin-bottom: 26px;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.44;
    @include respond-to(md) {
      margin-bottom: 35px;
      font-size: 24px;
    }
  }

  &__list {

  }
}

.other-list {
  display: grid;
  width: 100%;
  @include respond-to(md) {
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 27px;
  }

  &__item {
  }

  .other-item {
    margin-bottom: 47px;
    @include respond-to(md) {
      margin-bottom: 0;
    }

    &__image {
      margin-bottom: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: $beige;
      height: 280px;
      @include respond-to(md) {
        margin-bottom: 30px;
        height: 380px;
      }
    }

    &__name {
      margin-bottom: 11px;
      font-size: 18px;
      font-weight: 400;
      text-align: center;
      line-height: 1.44;
      @include respond-to(md) {
        margin-bottom: 14px;
        font-size: 24px;
        line-height: 42px;
        text-align: left;
      }
    }

    &__dscr {
      margin: 0 auto;
      font-weight: 400;
      font-size: 13px;
      line-height: 21px;
      color: #878787;
      max-width: 305px;
      text-align: center;
      @include respond-to(md) {
        margin: 0;
        font-size: 15px;
        text-align: left;
      }
    }
  }
}